@charset "UTF-8";

* {
  font-family: "Roboto", sans-serif; }

header {
  position: relative; }

nav {
  height: 50px;
  background-color: #212121; }

header nav {
  padding: 13px 70px; }

.menu {
  display: flex;
  justify-content: space-between;
  list-style: none;

  .menu_link {
    color: #ffffff;
    font-size: 15px;
    font-weight: 300;
    font-style: normal;
    letter-spacing: normal; }

  .menu_item {
    border-right: 2px dotted #7a7a7a;
    padding-right: 30px;
    padding-left: 30px; } }

.subheader {
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  width: 100%;

  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  .subheader_official {
    margin-top: 6px;
    opacity: 0.5;
    color: #ffffff;
    font-size: 13px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 68px;
    text-align: left;

    /* Text style for "О, фициаль" */
    font-style: normal;
    letter-spacing: normal;
    line-height: normal; }

  .subheader_call {
    text-align: right;
    margin-top: 21px;
    color: #ffffff;
    font-size: 12px;
    font-weight: 300;
    font-style: normal;
    letter-spacing: normal;
    line-height: 14px;
    text-align: right;

    /* Text style for "Звоните на" */
    font-style: normal;
    letter-spacing: normal;
    line-height: normal; }

  .subheader_phone {
    display: block;
    text-align: right;
    margin-top: 8px;
    color: #ffffff;
    font-size: 22px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 68px;
    text-align: center;
    text-transform: uppercase;

    /* Text style for "8 495 797" */
    font-style: normal;
    letter-spacing: normal;
    line-height: normal;
    line-height: 12px; }

  .subheader_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-family: Roboto;
    font-size: 14px;
    font-weight: 400;
    font-style: normal;
    letter-spacing: normal;
    line-height: 68px;
    text-align: center;
    text-transform: uppercase;

    /* Text style for "заказать з" */
    font-style: normal;
    letter-spacing: normal;
    line-height: normal; } }

.promo {
  min-height: 800px;
  background: url(/src/img/main.png) center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  .promo_header {
    text-align: center;
    color: #ffffff;
    font-weight: 700;
    font-style: normal;
    letter-spacing: normal;
    line-height: 60px;
    text-align: center;
    text-transform: uppercase;
    font-size: 60px;
    font-style: normal;
    letter-spacing: normal;
    margin: 0; }

  .promo_subheader {
    text-align: center;
    font-size: 36px;
    color: #ffffff;
    font-weight: 700;
    font-style: normal;
    letter-spacing: normal;
    line-height: 35px;
    margin: 0;
    margin-top: 22px; }

  .promo_descr {
    margin-top: 23px;
    text-align: center;
    color: #ffffff;
    font-size: 18px;
