.mobile {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background: url(/src/img/Rectangle_20.png) center center/cover no-repeat;
    .row {
        margin-top: 32px; }
    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 92px;
        &_1 {
            background: url(/src/img/2-layers.png) center center/cover no-repeat; }
        &_2 {
            background: url(/src/img/2-layers.png) center center/cover no-repeat; }
        &_3 {
            background: url(/src/img/2-layers.png) center center/cover no-repeat; }
        &_4 {
            background: url(/src/img/2-layers.png) center center/cover no-repeat; }
        &_5 {
            background: url(/src/img/2-layers.png) center center/cover no-repeat; }
        &_6 {
            background: url(/src/img/2-layers.png) center center/cover no-repeat; }
        &_subtitle {
            color: #ffffff;
            font-size: 24px;
            font-weight: 400;
            font-style: normal;
            letter-spacing: normal;
            line-height: 31px;
            text-align: center;
            text-transform: uppercase;
            font-style: normal;
            letter-spacing: normal;
            line-height: normal; }
        &_plus {
            position: absolute;
